nimation:breathe-text 10s ease infinite}}.bar{background:var(--search-color);grid-area:bar;display:grid;grid-template-columns:3em 1fr;grid-gap:auto;gap:auto;align-items:center;min-height:3em}.bar>img{box-sizing:border-box;height:1.5em;padding-left:1em;padding-right:2em}.bar>input{justify-self:flex-end;margin-right:2em}.pokemon-list>a{margin:0 auto}.pokemon-list>a:link,.pokemon-list>a:visited{color:inherit;text-decoration:none}.pokemon{display:grid;grid-template-rows:10em 2em;align-items:center;background-color:rgba(117,119,138,.3)}.pokemon>p{text-align:center}.pokemon>img{height:100%;margin:0 auto}.pokemon:hover{background-color:#5a5b66}.evol-container{display:flex;flex-direction:row}.evol-flex{display:flex;flex-direction:column;position:relative;padding-left:2em}.evolution{align-self:center}.evolution>a:link,.evolution>a:visited{color:inherit;text-decoration:none}.evolution>a>img{height:5em;-webkit-clip-path:circle(50% at 50% 50%);clip-path:circle(50% at 50% 50%);background-color:#5a5b66}.evolution>a>p{text-align:center;font-size:.5rem;text-transform:capitalize}.evolution+.evol-flex::before{content:"->";position:absolute;top:50%;left:0;-webkit-transform:translate(0, -50%);transform:translate(0, -50%)}
